<script>
import LocalThumbnail from './LocalThumbnail';

// // import { upload } from './file-upload.service';

// const STATUS_INITIAL = 0;
// const STATUS_SAVING = 1;
// const STATUS_SUCCESS = 2;
// const STATUS_FAILED = 3;


import Treeselect from '@riophae/vue-treeselect'
import axios from 'axios';
// import the styles
import '@riophae/vue-treeselect/dist/vue-treeselect.css'
import UserMixin from '@/mixins/UserMixin';
import { FluroContentForm, FluroContentRender } from 'fluro-vue-ui';


export default {
    props: {
        eventID: {
            type: String,
            required: true,
        },
    },
    components: {
        Treeselect,
        LocalThumbnail,
        FluroContentForm,
        FluroContentRender,
    },
    mixins: [UserMixin],
    data() {

        return {

            tagEntries: [],
            tagLoading: false,
            tagSearch: null,
            eventLoading:true,


            state: 'ready',
            tags: [],
            freeTags: [],
            realms: [],
            selection: [],
            files: [],
            search: "", //sync search
            prevSelected: -1, //index of previously selected item
            bytesLoaded: 0,
            bytesTotal: 0,
            cancelTokens: [],
            photographerSearch: '',
            photographer: null,
            photographerSearching: false,
            photographers: [],
            flatRealms: [],
            blankModel: {},

            //     //     uploadError: null,
            //     //     currentStatus: null,
            //     //     uploadFieldName: 'photos'
        }
    },
    computed: {
        extraFields() {
            return _.filter(this.photoDefinition.fields, function(field) {
                switch (field.key) {
                    case 'photographer':
                    case 'event':
                        return;
                        break;
                }

                return true;
            })
        },
        canFreeTag() {
            return this.$fluro.access.can('create', 'tag');
        },
        canSearchTag() {
            return true;
            //     return this.$fluro.access.can('view any', 'tag') || this.$fluro.access.can('view own', 'tag');
        },

        canSearchPhotographers() {
            return this.$fluro.access.can('view', 'contact');
        },
        readableSelection() {

            var targets = this.targets;
            var items = 'images';
            if (targets.length == 1) {
                items = 'image';
            }

            if (targets.length == this.files.length) {
                return `(All ${targets.length} ${items})`;
            } else {
                return `(${targets.length} ${items})`;
            }
        },
        targets() {
            var self = this;


            // //console.log('GET TARGETS', self.selection.length, self.files.length);
            ///////////////////////////////////

            if (self.selection && self.selection.length) {

                return self.selection;
            } else {


                return self.files;
            }
        },
        progress() {
            var self = this;

            var percent = Math.floor((self.bytesLoaded * 100) / self.bytesTotal);
            return percent;
        },
    },
    //     isInitial() {
    //         return this.currentStatus === STATUS_INITIAL;
    //     },
    //     isSaving() {
    //         return this.currentStatus === STATUS_SAVING;
    //     },
    //     isSuccess() {
    //         return this.currentStatus === STATUS_SUCCESS;
    //     },
    //     isFailed() {
    //         return this.currentStatus === STATUS_FAILED;
    //     }
    // },
    methods: {


        definitionTags(array) {

            var self = this;

            return _.chain(array)
                .reduce(function(set, item) {

                    var _type = item.definition || 'tag';
                    if (!set[_type]) {
                        set[_type] = {
                            title: self.$fluro.types.readable(_type, true, 'tag'),
                            items: [],
                        }
                    }

                    set[_type].items.push(item);

                    return set;
                }, {})
                .values()
                .value();
        },
        unlinkPhotographer(file, $event) {
            console.log('Delete and unlink', file, $event)
            file.data.photographer = null;

            this.$forceUpdate();
        },
        canStart() {

            var hasFiles = _.some(this.files, function(file) {
                return file.state != 'complete' && file.state != 'progress';
            })

            return hasFiles && this.state != 'uploading'
        },
        start() {

            var self = this;

            if (!this.realms || !this.realms.length) {
                self.$toasted.show(`Please select at least one realm`, {
                    // icon:'check'
                    type: 'warning',
                })
                return;
            }

            this.deselectAll();
            this.state = 'uploading';

            this.uploadNext();
        },

        uploadNext() {

            var self = this;

            //If we aren't cancelled
            if (self.state != 'uploading') {
                return
            }


            var nextItem = _.find(this.files, function(file) {
                return file.state != 'complete' && file.state != 'progress';
            })

            if (nextItem) {
                this.upload(nextItem);
            } else {
                //console.log('All files uploaded!')

                self.$toasted.show(`All photos have been uploaded`, {
                    // icon:'check'
                    duration: 10000,
                    type: 'success',
                })

                self.state = 'ready';
            }

        },
        cancel() {
            this.state = 'cancelled';
            this.cancelInflightRequests();
        },
        cancelInflightRequests() {
            _.each(this.cancelTokens, function(source) {
                source.cancel('Operation canceled by the user.');
            })

            this.cancelTokens = [];

        },
        addMore() {
            //console.log('REFS', this.$refs);

            var fileInput = this.$refs.file;
            if (fileInput) {
                fileInput.click();
            }
        },
        upload(file) {

            var self = this;

            switch (file.state) {
                case 'complete':
                case 'progress':
                    //Stop here if it's already in progress or complete
                    return;
                    break;
                default:
                    break;
            }

            /////////////////////////////////////////////

            //Change the state to progress
            file.state = 'progress';

            //Create a new form object
            var formData = new FormData();
            var jsonData = {};

            // //console.log('FORM', file, this);

            /////////////////////////////////////////////

            //Include the tags
            jsonData.definition = 'photo';
            jsonData.tags = [].concat(file.tags, file.freeTags);

            //Data
            var data = file.data || {}

            //Link the photo to the event
            data.event = self.$route.params.id
            data.photographer = self.$fluro.utils.getStringID(data.photographer) || null;
            jsonData.data = data;


            //Include the realms
            jsonData.realms = this.realms;

            /////////////////////////////////////////////

            //Add the JSON data and the binary file data
            formData.set('json', JSON.stringify(jsonData));
            formData.append('file', file.file, file.name)

            /////////////////////////////////////////////

            self.$forceUpdate();

            var body = formData;
            var config = {
                headers: {
                    'Content-Type': 'multipart/form-data'
                },
                onUploadProgress: progressEvent => {
                    let percentCompleted = Math.floor((progressEvent.loaded * 100) / progressEvent.total);
                    // do whatever you like with the percentage complete
                    // maybe dispatch an action that will update a progress bar or something
                    file.progress = percentCompleted;

                    file.bytesLoaded = progressEvent.loaded;
                    file.bytesTotal = progressEvent.total;

                    // //console.log('Force an update')

                    self.bytesLoaded = _.reduce(self.files, function(set, file) {
                        if (file.state == 'complete') {
                            set += file.size || file.bytesTotal || 0;
                        } else {
                            set += file.bytesLoaded || 0;
                        }
                        return set;
                    }, 0)

                    //console.log('Bytes loaded now', self.bytesLoaded, progressEvent.loaded);

                    self.$forceUpdate();
                }
            };

            ///////////////////////////////////////////////////

            const CancelToken = axios.CancelToken;
            const source = CancelToken.source();
            config.cancelToken = source.token;

            self.cancelTokens.push(source);


            ///////////////////////////////////////////////////

            return this.$fluro.api.post('/file/upload', body, config)
                .then(function(res) {
                    //console.log('RESPONSE', res);
                    file.state = 'complete';
                    file.result = res.data;

                    self.uploadNext();
                    self.$forceUpdate();
                })
                .catch(function(err) {
                    //console.log('Error', err);
                    file.state = 'error';
                    self.uploadNext();
                    self.$forceUpdate();
                });

        },
        selectWithinRange(num1, num2) {

            var self = this;
            var start;
            var end;

            ///////////////////////////////////////

            if (num1 > num2) {
                start = num2;
                end = num1 + 1;
            } else {
                start = num1;
                end = num2;
            }

            ///////////////////////////////////////

            _.each(self.files, function(item, i) {

                if (_.inRange(i, start, end)) {

                    if (!item.isSuccess) {
                        self.select(item);
                    }

                }
            })
        },

        isSelected(item) {
            return _.includes(this.selection, item);
        },
        select(item) {
            if (!this.isEditable(item)) {
                return;
            }
            if (this.isSelected(item)) {
                return;
            }

            this.selection.push(item);
            this.clearDataModel()
        },
        deselect(item) {
            var index = this.selection.indexOf(item);
            if (index != -1) {
                this.selection.splice(index, 1);
            }
            this.clearDataModel()
        },
        clearSelection() {
            var self = this;
            _.each(this.selection, function(item) {
                self.remove(item);
            })
            this.selection.length = 0;
            this.$forceUpdate();
            this.clearDataModel()
        },
        clearAll() {
            this.files.length = 0;
            this.selection.length = 0;
            this.$refs.file.value = '';
            this.$forceUpdate();
            this.clearDataModel()
        },
        remove(item) {
            var index = this.files.indexOf(item);
            if (index != -1) {
                this.files.splice(index, 1);
                this.$forceUpdate();
                this.clearDataModel()
            }
        },
        deselectAll() {
            this.selection = [];
            // this.selection.$set('length', 0);
            // .length = 0;
            this.$forceUpdate();
            this.clearDataModel()
        },
        selectAll() {

            var self = this;

            _.each(self.files, function(item, i) {
                if (!item.isSuccess) {
                    self.select(item);
                }
            })

            this.$forceUpdate();
            this.clearDataModel()


        },
        toggle(item, $event, $index) {



            var self = this;

            // if (item.isSuccess || $scope.uploader.isUploading) {
            //     return;
            // }

            if ($event) {
                $event.preventDefault();
            }

            //Open the result
            if (item.result) {
                return;
            }

            // shift + click listener
            if ($event.shiftKey && self.prevSelected >= 0) {
                self.selectWithinRange($index, self.prevSelected);
                self.prevSelected = $index;
                // select or deselect item
            } else {

                if ($event.metaKey) {

                    //console.log('MetaKey!', )
                    //If it's selected already then deselect it
                    if (self.isSelected(item)) {

                        //console.log('Deselect!');
                        self.deselect(item)
                        self.prevSelected = -1;
                    } else {
                        self.select(item)
                        // item.selected = true;
                        self.prevSelected = $index;
                    }
                } else {
                    self.prevSelected = -1;
                    _.each(self.files, function(other) {
                        if (other == item) {
                            self.select(other)
                            // other.selected = true;
                            self.prevSelected = $index;
                        } else {
                            self.deselect(other)
                            // other.selected = false;
                        }
                    })
                }
            }
        },
        isEditable(file) {

            if (this.state == 'uploading') {
                return;
            }

            switch (file.state) {
                case 'progress':
                case 'complete':
                    return;
                    break;
                default:
                    return true;
                    break;
            }

        },

        updatePhotographer() {
            var self = this;

            if (!self.photographer) {
                return;
            }


            ///////////////////////////////////

            //Apply the photographer to each photo
            _.chain(self.targets)
                .filter(function(file) {
                    return self.isEditable(file);
                })
                .map(function(photo) {
                    if (!photo.data) {
                        photo.data = [];
                    }

                    photo.data.photographer = self.photographer;
                })
                .value();

            ///////////////////////////////////

            self.photographerSearch = "";
            self.photographer = null;


        },
        clearDataModel() {
            var self = this;
            // self.blankModel = {};
            self.$set(self, 'blankModel', {});
            self.$refs.form.reset();

            // console.log('Clear the data', self.$refs.form)

        },
        updateData() {
            var self = this;
            var targets = self.targets;


            // var newData = JSON.parse(JSON.stringify(self.blankModel))


            ///////////////////////////////////

            _.chain(targets)
                .filter(function(file) {
                    return self.isEditable(file);
                })
                .map(function(photo) {
                    if (!photo.data) {
                        photo.data = {}
                    }

                    _.each(self.blankModel, function(update, key) {
                        // photo.data[key] = data;
                        self.$set(photo.data, key, update);
                    });
                    // self.$set(photo, 'data', update);

                    // console.log('APPLY', photo);

                    return photo;
                })
                .value();

            ///////////////////////////////////

            //console.log('APPLIED TO', targets);

        },
        updateTags() {

            var self = this;

            var targets = self.targets;

            ///////////////////////////////////


            _.chain(targets)
                .filter(function(file) {
                    return self.isEditable(file);
                })
                .map(function(photo) {
                    if (!photo.tags) {
                        photo.tags = [];
                    }

                    if (!photo.freeTags) {
                        photo.freeTags = [];
                    }

                    /////////////////////////////////////////

                    photo.tags = _.chain(photo.tags.concat(self.tags))
                        .compact()
                        .uniqBy(function(tag) {
                            // //console.log('TAG', tag)
                            return String(tag.title.toLowerCase());
                        })
                        .value();

                    /////////////////////////////////////////

                    photo.freeTags = _.chain(photo.freeTags.concat(self.freeTags))
                        .compact()
                        .uniqBy(function(tag) {
                            return String(tag.toLowerCase());
                        })
                        .value();
                })
                .value();

            ///////////////////////////////////

            self.search = "";
            self.tags = [];
            self.freeTags = [];

        },
        removeFreeTag(item, tag) {
            var index = item.freeTags.indexOf(tag);
            if (index != -1) {
                item.freeTags.splice(index, 1);
                this.$forceUpdate();
            }
        },
        removeTag(item, tag) {
            var index = item.tags.indexOf(tag);
            if (index != -1) {
                item.tags.splice(index, 1);
                this.$forceUpdate();
            }
        },
        close() {
            if (window.history.length) {
                this.$router.go(-1)
            } else {
                this.$router.push({ query: Object.assign({}, this.$route.query, { photo: undefined }) });
            }
        },
        createObjectURL(file) {
            return (window.URL) ? window.URL.createObjectURL(file) : window.webkitURL.createObjectURL(file);
        },
        getBase64Thumb(img, width) {
            var canvas = document.createElement('canvas'),
                ctx = canvas.getContext('2d');

            canvas.width = width;
            canvas.height = canvas.width * (img.height / img.width);

            /// step 1
            var oc = document.createElement('canvas'),
                octx = oc.getContext('2d');

            oc.width = img.width * 0.5;
            oc.height = img.height * 0.5;
            octx.drawImage(img, 0, 0, oc.width, oc.height);

            /// step 2
            octx.drawImage(oc, 0, 0, oc.width * 0.5, oc.height * 0.5);

            ctx.drawImage(oc, 0, 0, oc.width * 0.5, oc.height * 0.5,
                0, 0, canvas.width, canvas.height);

            return canvas.toDataURL('image/jpeg', 0.5);
        },
        reset() {
            this.files.length = 0;
            this.cancelInflightRequests();

            this.state = 'ready';
            this.tags = [];
            this.realms = [];
            this.search = '';
            this.selection = [];
            this.files = [];
            this.prevSelected = -1;
            this.bytesLoaded = 0;
            this.bytesTotal = 0;
            this.cancelTokens = [];
        },
        renderNextThumbnail() {

            var nextThumbnail = _.find(this.files, function(file) {
                return !file.local;
            })

            if (nextThumbnail) {
                this.renderThumbnail(nextThumbnail);
            } else {
                //console.log('All thumbnails rendered')
            }

        },

        renderThumbnail(file) {
            //Set the local url so that it start's rendering
            file.local = this.createObjectURL(file.file);
        },

        promiseReceived(file, promise) {

            var self = this;
            //Update the file
            promise.then(function(res) {
                file.loaded = true;
                self.renderNextThumbnail()
                self.$forceUpdate();
            })
            // //console.log('PREVIEW LOADED EVENT', file, promise);

            // file.loaded = true;
        },


        filesChange(fieldName, fileList) {

            var self = this;
            // handle file changes


            if (!fileList.length) return;



            /////////////////////////////////////////

            _.each(fileList, function(file) {

                ///////////////////////////

                //Get the first contact linked to the current logged in user
                //If there is one
                var photographer;
                var photographerID = _.chain(self)
                    .get('user.contacts')
                    .compact()
                    .first()
                    .value();

                //Photographer id
                if (photographerID) {
                    photographer = {
                        _id: photographerID,
                        title: `${self.user.firstName} ${self.user.lastName}`,
                        firstName: self.user.firstName,
                        lastName: self.user.lastName,
                    }
                }

                ///////////////////////////

                //Create the data object
                //and expect the uploader to be the photographer
                //by default
                var data = {
                    photographer,
                };

                //Create the queue item ready to upload
                self.files.push({
                    name: file.name,
                    size: file.size,
                    webkitRelativePath: file.webkitRelativePath,
                    tags: [],
                    freeTags: [],
                    //local: self.createObjectURL(file),//do this sequentially
                    file,
                    data,
                });
            })

            /////////////////////////////////////////

            //Update the total filesize of all files
            self.bytesTotal = _.reduce(self.files, function(set, file) {
                set += file.bytesTotal || file.size || 0;
                return set;
            }, 0);

            /////////////////////////////////////////

            this.renderNextThumbnail();

            // //console.log('files', self.files);

            // // append the files to FormData
            // Array
            //     .from(Array(fileList.length).keys())
            //     .map(x => {
            //         formData.append(fieldName, fileList[x], fileList[x].name);
            //     });

            // save it
            // this.save(formData);

            // this.files = fileList;
        }
    },
    mounted() {
        this.reset();
    },
    beforeDestroy() {
        this.reset();

    },
    watch: {
        'photographerSearch': _.debounce(function() {

            var self = this;
            var terms = self.photographerSearch;

            self.photographers = [];
            self.photographerSearching = false;

            //No terms or we already know
            if (!terms || !terms.length) {
                return;
                // && this.querySelections(val)
            }



            // var search = self.photographerSearch;
            self.photographerSearching = true;
            //console.log('Search now', terms);

            // //////////////////////////////

            // if (!search || !search.length) {
            //     self.photographerSearching = false;
            //     self.photographers = [];
            //     //Clear out
            //     return;
            // }

            //////////////////////////////

            // self.$fluro.api.get(`/content/contact/search/${search}`, {
            self.$fluro.content.search(terms, {
                    limit: 4,
                    select: 'title',
                    types: ['contact'],
                })
                .then(function(results) {

                    self.photographers = results; //_.map(results, 'title');
                    self.photographerSearching = false;
                    // self.photographerSearch = '';
                })
            // .catch(function(err) {

            //     self.photographers = [];
            //     self.photographerSearching = false;
            //     // self.photographerSearch = '';
            // })
        }, 500)

    },
    asyncComputed: {
        photoDefinition: {
            get() {

                var self = this;



                return new Promise(function(resolve, reject) {

                    return self.$fluro.types.get('photo').then(function(result) {
                        return resolve(result.definition);
                    }).catch(reject);
                })
            }
        },
        tagDefinitions: {
            default: [],
            get() {
                var self = this;

                return new Promise(function(resolve, reject) {

                    return self.$fluro.api.get('/tags/tree').then(function(res) {
                        return resolve(res.data);
                    })
                });
            }
        },
        realmOptions: {
            default: [],
            get() {

                var self = this;
                var eventID = this.$route.params.id;

                return new Promise(function(resolve, reject) {

                    self.$fluro.access.retrieveSelectableRealms('create', 'photo', 'image').then(function(res) {




                        function mapChild(realm) {

                            realm.id = realm._id;
                            realm.label = realm.title;
                            realm.open = true;


                            if (realm.children && realm.children.length) {
                                realm.children = _.map(realm.children, function(child) {
                                    return mapChild(child);
                                })
                            } else {
                                delete realm.children;
                            }

                            return realm;
                        }

                        //////////////////////////////////////////

                        var mapped = _.chain(res)
                            .reduce(function(set, realmType) {

                                var typeID = realmType._discriminator || realmType._discriminatorType || realmType.definition || 'realm';

                                //////////////////////////////////

                                var existing = set[typeID];



                                //////////////////////////////////

                                var mappedChildren = _.chain(realmType.realms)
                                    .map(function(realm) {
                                        return mapChild(realm);
                                    })
                                    .value();

                                //////////////////////////////////

                                if (!existing) {
                                    existing = set[typeID] = {
                                        label: realmType.plural,
                                        id: typeID,
                                        children: mappedChildren,
                                        isDisabled: true,
                                    }
                                }

                                //////////////////////////////////

                                return set;

                            }, {})
                            .values()
                            .value();

                        //////////////////////////////////////////

                        return resolve(mapped);



                        // var mapped = _.chain(res)
                        //     .reduce(function(set, realm) {

                        //         mapChild(realm);

                        //         var disc = realm._discriminator || realm._discriminatorType || realm.definition || 'realm';

                        //         if (!set[disc]) {
                        //             set[disc] = {
                        //                 label: disc,
                        //                 id: disc,
                        //                 children: [],
                        //                 isDisabled: true,
                        //             };
                        //         }

                        //         set[disc].children.push(realm);

                        //         return set;
                        //     }, {})
                        //     .values()
                        //     .value();

                        // if (mapped.length == 1) {
                        //     mapped = mapped[0].children;
                        // }


                        // //console.log('RES', res, mapped);

                        resolve(mapped);
                    });


                });
            }
        },
        event: {
            default: {},
            get() {

                var self = this;
                var eventID = this.eventID;
                self.eventLoading = true;

                /////////////////////////////////////////////

                return new Promise(function(resolve, reject) {

                    self.$fluro.content.get(eventID, {
                            select: 'title startDate endDate realms'
                        })
                        .then(function(event) {
                            resolve(event);

                            self.realms = self.$fluro.utils.arrayIDs(event.realms);
                            self.eventLoading = false;
                            //console.log('Use event realms')
                        })
                        .catch(function(err) {

                            console.log(err);
                            self.eventLoading = false;

                            // return self.$router.replace({
                            //     name: 'upload',
                            // })
                        });


                });
            }
        }

    }
}
</script>
